<template>
  <div class="good">
    <div class="good_wrapper">
      <div class="left_nav" ref="left_nav">
        <div class="goods_wrapper">
          <ul v-if="goods.length">
            <li
              v-for="(good, $index) in goods"
              class="nav_item"
              ref="nav_item"
              :class="{active: $index ===item_index}"
              @click="changeIndex($index, $event)"
            >
              <div class="name_wrapper">
                <supports-type-class
                  v-if="good.type !== -1"
                  :sunpportsType="good.type"
                  class="icon_wrapper"
                ></supports-type-class>
                <span>{{good.name}}</span>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="right_content" ref="right_content">
        <div class="right_content_hook">
          <ul v-if="goods.length">
            <li v-for="good in goods" class="good_item" ref="good_item">
              <h2 class="title">{{good.name}}</h2>
              <ul>
                <li v-for="food in good.foods" class="good_info_wrapper">
                  <div class="avatar" @click="toggleDetail(food)">
                    <img v-lazy="food.icon" width="60px" />
                  </div>
                  <div class="info">
                    <div class="name_wrapper" @click="toggleDetail(food)">
                      <h2 class="name">{{food.name}}</h2>
                      <div class="description">{{food.description}}</div>
                      <div class="sell_info">
                        <span class="sellCount">月售{{food.sellCount}}</span>
                        <span class="rate">好评率{{food.rating}}%</span>
                      </div>
                      <div class="pay">
                        <span class="now_price">￥{{food.price}}</span>
                        <span class="old_price" v-if="food.oldPrice">￥{{food.oldPrice}}</span>
                      </div>
                    </div>
                  </div>
                  <increase-and-reduce @totalPrice="totalPrice" :food="food" :ballData="ballData"></increase-and-reduce>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="cart_shop">
      <cart-shop
        :sleted_data="seletedData"
        :seller="seller"
        ref="cat_shop"
        @showGoodsMenu="showGoodsMenu"
      ></cart-shop>
    </div>
    <div class="cat_menu_wrapper">
      <cat-menu :seleted_data="seletedData" @empty="empty" ref="cat_menu"></cat-menu>
    </div>
    <div class="good_detail_warpper" ref="good_detail_warpper">
      <good-detail
        :rating_food="rating_food"
        @totalPrice="totalPrice"
        @toggleDetailWrapper="toggleDetailWrapper"
        :detail_show="detail_show"
      ></good-detail>
    </div>
  </div>
</template>
<script type="text/javascript">
import supportsTypeClass from "@/components/supportsTypeClass/supportsTypeClass";
import cartShop from "@/components/cart_shop/cart_shop";
import increaseAndReduce from "@/components/increaseAndReduce/increaseAndReduce";
import catMenu from "@/components/cat_menu/cat_menu";
import goodDetail from "@/components/good_detail/good_detail";
import BScroll from "better-scroll";
export default {
  props: {
    goods: {
      type: Array
    },
    seller: {
      type: Object
    }
  },

  data() {
    return {
      item_index: 0,
      good_item_ele: [],
      good_items_pos: [],
      seletedData: [],
      ballData: [],
      rating_food: {},
      detail_show: false
    };
  },

  created() {
    //  this.init_BSscroll()
  },
  mounted() {
    this.init_BSscroll();
    this.$nextTick(() => {
        this.good_item_ele = this.$refs.good_item;
        this.$refs.good_item.map(item => {
          this.good_items_pos.push(item.offsetTop);
        });
      });
  },
  methods: {
    empty() {
      this.seletedData.splice(0);
      for (let i = 0; i < this.goods.length; i++) {
        let foodsLen = this.goods[i].foods.length || 1;
        for (let k = 0; k < foodsLen; k++) {
          this.$set(this.goods[i].foods[k], "count", 0);
          delete this.goods[i].foods[k].count;
        }
      }
    },
    toggleDetail(food) {
      this.rating_food = food;
      this.detail_show = !this.detail_show;
    },
    toggleDetailWrapper() {
      this.detail_show = !this.detail_show;
    },
    showGoodsMenu(val) {
      if (this.seletedData.length) {
        this.$nextTick(() => {
          this.$refs.cat_menu.toggleMenu(val);
        });
      }
    },
    changeIndex(index, ev) {
      this.item_index = index;
      this.leftScroll.scrollToElement(ev.target, 500, false, true);
      this.rightScroll.scrollTo(0, -this.good_items_pos[index], 500);
    },

    totalPrice(valArr) {
      this.$nextTick(() => {
        if (valArr[2]) {
          this.$refs.cat_shop.dorpBall(valArr[2].target);
        }
      });
      for (let i = 0; i < this.goods.length; i++) {
        let good = this.goods[i];

        let good_len = good.foods.length || 1;

        for (let k = 0; k < good_len; k++) {
          if (this.goods[i].foods[k] === valArr[1]) {
            if (good.foods[k].count === 1) {
              good.foods[k].count = valArr[0];
              return this.seletedData.push(good.foods[k]);
            } else {
              let index = this.seletedData.findIndex(item => {
                return item.name === good.foods[k].name;
              });
              if (valArr[0] !== 0) {
                good.foods[k].count = valArr[0];
                this.seletedData.splice(index, 1, good.foods[k]);
              } else {
                this.seletedData.splice(index, 1);
              }
            }
          }
        }
      }
    },

    init_BSscroll() {
      this.$nextTick(() => {
        this.leftScroll = new BScroll(this.$refs.left_nav, {
          scrollY: true,
          click: true
        });

        this.rightScroll = new BScroll(this.$refs.right_content, {
          scrollY: true,
          click: true,
          probeType: 3
        });
        if (!(this.good_items_pos && this.good_items_pos.length)) return;
        this.rightScroll.on("scroll", pos => {
          let { x, y } = pos;
          let good_items_pos_arr = this.good_items_pos || [];
          let nav_items = this.$refs.nav_item;
          let goods_leng = good_items_pos_arr.length;
          if (goods_leng > 0) {
            for (let i = 0; i < good_items_pos_arr.length - 1; i++) {
              if (
                Math.abs(y) >= good_items_pos_arr[i] &&
                Math.abs(y) < good_items_pos_arr[i + 1]
              ) {
                this.item_index = i;
                this.leftScroll.scrollToElement(nav_items[i], 500, false, true);
              }
            }
            if (Math.abs(y) >= good_items_pos_arr[goods_leng - 1]) {
              this.item_index = goods_leng - 1;
              this.leftScroll.scrollToElement(
                nav_items[goods_leng - 1],
                500,
                false,
                true
              );
            }
          }
        });
      });
    }
  },

  watch: {
    goods: {
      handler () {
        if (!this.leftScroll) {
          this.$nextTick(() => {
            this.leftScroll = new BScroll(this.$refs.left_nav, {
              scrollY: true
            });
          });
        } else {
          this.$nextTick(() => {
            this.leftScroll.refresh();
          });
        }

        if (!this.rightScroll) {
        
          this.rightScroll = new BScroll(this.$refs.right_content, {
            scrollY: true,
            click: true,
            probeType: 3
          });

          this.rightScroll.on("scroll", pos => {
            let { x, y } = pos;
            let good_items_pos_arr = this.good_items_pos;
            let nav_items = this.$refs.nav_item;
            let goods_leng = good_items_pos_arr.length;
            for (let i = 0; i < good_items_pos_arr.length - 1; i++) {
              if (
                Math.abs(y) >= good_items_pos_arr[i] &&
                Math.abs(y) < good_items_pos_arr[i + 1]
              ) {
                this.item_index = i;
                this.leftScroll.scrollToElement(nav_items[i], 500, false, true);
              }
            }
            if (Math.abs(y) >= good_items_pos_arr[goods_leng - 1]) {
              this.item_index = goods_leng - 1;
              this.leftScroll.scrollToElement(
                nav_items[goods_leng - 1],
                500,
                false,
                true
              );
            }
          });
        } else {
          this.$nextTick(() => {
            this.rightScroll.refresh();
          });
        }

        this.$nextTick(() => {
          console.log(3333333333)
          this.good_item_ele = this.$refs.good_item;
          this.$refs.good_item.map(item => {
            this.good_items_pos.push(item.offsetTop);
          });
        });
      },
      deep: true
    }
  },

  components: {
    supportsTypeClass,
    cartShop,
    increaseAndReduce,
    catMenu,
    goodDetail
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/mixin.styl';

.good_wrapper {
  display: flex;
  position: absolute;
  padding-bottom: 40px;
  z-index: -3;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  box-sizing: border-box;
  padding-top: 174px;
  overflow: hidden;

  .left_nav {
    flex: 0 0 80px;
    overflow: hidden;

    .nav_item {
      position: relative;
      display: flex;
      align-items: center;
      height: 54px;
      line-height: 54px;
      padding: 0 12px;
      font-size: 0px;
      background-color: #f3f5f7;

      &::after {
        content: '';
        position: absolute;
        box-sizing: border-box;
        // margin: 0 12px
        left: 0;
        bottom: 0;
        width: 56px;
        margin-left: 12px;
        border-1px(rgba(7, 17, 27, 0.1));
      }

      &.active {
        margin-top: -1px;
        padding-top: 1px;
        font-size: 14px;
        color: rgb(7, 17, 27);
        background-color: #fff;
      }

      &.active::after {
        border-none();
      }

      .name_wrapper {
        display: inline-block;
        font-size: 12px;
        line-height: 14px;
        font-weight: 200;
        color: rgb(77, 85, 93);

        .icon_wrapper {
          display: inline-block;
          margin-right: 2px;
          width: 12px;
          height: 12px;
          background-position: center center;
          background-repeat: no-repeat;
          background-size: 12px 12px;
          vertical-align: top;
        }

        .name {
          vertical-align: top;
        }
      }
    }
  }

  .right_content {
    flex: 1;
    overflow: hidden;

    .good_item {
      .title {
        margin-bottom: 18px;
        height: 26px;
        line-height: 26px;
        padding-left: 14px;
        box-sizing: border-box;
        border-left: 1px solid #d9dde1;
        background-color: #f3f5f7;
        font-size: 12px;
        color: rgb(147, 153, 159);
        font-weight: 200;
      }

      .good_info_wrapper {
        position: relative;
        display: flex;
        margin-left: 18px;
        padding-bottom: 18px;
        box-sizing: border-box;
        margin-bottom: 18px;

        &::after {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border-1px(rgba(7, 17, 27, 0.1));
        }

        &:last-child {
          margin-bottom: 0px;

          &::after {
            content: '';
            border-none();
          }
        }

        .avatar {
          flex: 0 0 60;
          margin-right: 10px;
          vertical-align: top;
        }

        .info {
          flex: 1;
          vertical-align: top;

          .name {
            font-size: 14px;
            line-height: 14px;
            color: rgb(7, 17, 27);
          }

          .description, .sell_info {
            margin: 8px 0;
            font-size: 10px;
            line-height: 12px;
            // line-height: 10px
            color: rgb(147, 153, 159);

            .sellCount {
              margin-right: 12px;
            }
          }

          .pay {
            .now_price {
              margin-right: 12px;
              color: rgb(240, 20, 20);
              font-size: 14px;
              line-height: 24px;
              font-weight: 700;
            }

            .old_price {
              font-size: 10px;
              color: rgb(147, 153, 159);
              font-weight: normal;
              line-height: 24px;
            }
          }
        }
      }
    }
  }
}

.cart_shop {
  position: absolute;
  width: 100%;
  height: 48px;
  left: 0px;
  bottom: 0px;
}
</style>


