<template>
	<div :class="star_class" class="star">
		<span class="star_item" :class="itemclass" v-for="(itemclass, $index) in starClassArr"></span>
	</div>
</template>
<script type="text/javascript">
	const STAR_COUNT = 5
	export default {
	 props: {
	 	scopeNum: {
	 		type: Number,
	 		default: 0
	 	},
	 	star_size: {
	 		type: String,
	 		default: '48'
	 	} 
	 },

	 computed: {
	 	star_class() {
	 		return 'star_' + this.star_size
	 	},
	 	starClassArr() {
	 	  let {floor} = Math
	 	  let numScore = floor(this.scopeNum * 2) /2
	 	  let integer = floor(numScore)
	 	  let decimals = numScore % 1
	 	  let tepArr = []
	 	  for(let i = 0; i < integer; i++) {
	 	  	tepArr.push('on')
	 	  }
	 	  if(decimals){
	 	  	tepArr.push('half')
	 	  }
	 	  if(tepArr.length !== STAR_COUNT) {
	 	  	for(let i = tepArr.length; i < STAR_COUNT; i++) {
	 	  		tepArr.push('off')
	 	  	}
	 	  }
	 	  return tepArr
	 	}
	 }

	 
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/mixin.styl'
 .star
 .star_20
  .star_item		
   display: inline-block
   width: 10px
   height: 10px
   margin-right: 5px
   background-size: 10px 10px
   background-repeat: no-repeat
   background-position: center center
   &:last-child
    margin-right: 0px			
   &.on
    bg-img('./star24_on')
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star36_on')
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star48_on') 
    
   &.half
    bg-img('./star24_half')	
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star36_half')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star48_half') 
   &.off
    bg-img('./star24_off')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star36_off')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star48_off')
 .star_32
  .star_item	
   display: inline-block
   width: 15px
   height: 15px
   margin-right: 6px
   background-size: 15px 15px
   background-repeat: no-repeat
   background-position: center center
   &:last-child
    margin-right: 0px			
   &.on
    bg-img('./star24_on')
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star36_on')
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star48_on') 
    
   &.half
    bg-img('./star24_half')	
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star36_half')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star48_half') 
   &.off
    bg-img('./star24_off')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star36_off')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star48_off')  		
 .star_48
  .star_item
   display: inline-block
   width: 24px
   height: 24px
   margin-right: 24px
   background-size: 24px 24px
   background-repeat: no-repeat
   background-position: center center  
   &:last-child
    margin-right: 0px			
   &.on
    bg-img('./star24_on')
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star36_on')
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star48_on') 
    
   &.half
    bg-img('./star24_half')	
    @media(-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio: 2)
     bg-img('./star36_half')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star48_half') 
   &.off
    bg-img('./star24_off')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star36_off')
    @media(-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio: 3)
     bg-img('./star48_off') 	 	 	
</style>


