<template>
	<transition name="move" >
	<div class="good_detail" ref="good_detail" v-show="detail_show">
		<div class="BSscroll_wrapper">		
		<div class="avatar_warpper">
			<img class="avatar" :src="rating_food.icon">
			</img>
		</div>
		<div class="content">
			<div class="food_info">
				<h2 class="name">{{rating_food.name}}</h2>
				<div class="sell_rating">
					<span class="sellCount">
						月售{{rating_food.sellCount}}份
					</span>
					<span class="rating">
						好评率{{rating_food.rating}}%
					</span>
				</div>
				<div class="pay_price">
					<span class="price">
						￥{{rating_food.price}}
					</span>
					<span class="old_price" v-if="rating_food.oldPrice">
						￥{{rating_food.oldPrice}}
					</span>
				</div>
				<div class="shop_car shop_car_bg" v-show="!rating_food.count" @click="addOne">
					加入购物车
				</div>
				<div class="shop_car" v-show="rating_food.count" >
					<div class="increase_and_reduce">
						<increase-and-reduce @totalPrice="totalPrice"  :food="rating_food" ></increase-and-reduce>
					</div>
				</div>
				</div>
				<div class="line"></div>
				<div class="desc_info">
					<h2 class="title">
						商品介绍
					</h2>
					<div class="info">
						{{rating_food.info}}
					</div>
				</div>
				<div class="line"></div>
				<div class="detail_selected_warpper">
					<detail-selected :message="good_detail_message" :detailCountArr="detailCountArr" @toggleSelectedNum="toggleSelectedNum"></detail-selected>	
				</div>
				<div class="rating_content">
					<div class="selected_type">
						<i class="icon-check_circle" @click="toggleIconCheck" :class="{'active': icon_check_bool}"></i><span class="text">只看有内容的评价</span>
					</div>
					<ul class="rating_food_wapper
					 .rating_food
					  ">
						<li v-for="rating in detail_rating_arr" class="rating_food">
							<div class="rateTime">{{rating.rateTime | formatDate}}</div>
							<div class="text_wrapper">
								<i :class="{'icon-thumb_down': rating.rateType === 1, 'icon-thumb_up' : rating.rateType === 0}"></i>
								<span class="text">{{rating.text}}</span>
							</div>
							<div class="phone_icon">
								<span class="username">
									{{rating.username}}
								</span>
								<img :src="rating.avatar" width="12" height="12">
							</div>
						</li>
					</ul>
				</div>
			
			</div>

			<div class="back" @click="toggleShow">
			  <i class="icon-arrow_lift"></i>
		    </div>

		</div>
	</div>
</transition>
</template>
<script type="text/javascript">
	 import detailSelected from '@/components/detail_selected/detail_selected'
	 import increaseAndReduce from '@/components/increaseAndReduce/increaseAndReduce'
	 import {formatDate} from 'common/js/formatDate.js'
	 import BScroll from 'better-scroll'
	
	export default {
		props: {
			'rating_food': {
				type: Object
			},
			'detail_show': {
				type: Boolean
			}
			
		},
		data() {
 			return {
 				good_detail_message: {
 					all: '全部',
 					positive: '推荐',
 					negative: '吐槽'
 				},
 				selected_content_type: 0,
 				content_bool: 0,
 				icon_check_bool: 0,
 				car_status: true,
 				tem_good: {}
 			}
		},

		components: {
			detailSelected,
			increaseAndReduce
		},

		created() {
			this.init_detal_scroll()
		},

		computed: {
			detail_rating_arr(){
					let all_arr = []
					if(this.content_bool === 1) {
						this.rating_food.ratings.map(food => {
							if(food.text) {
								all_arr.push(food)
							}
						})
					}else {
						all_arr = this.rating_food.ratings
					}
					switch (this.selected_content_type) {
					case 0:
						// return this.rating_food.ratings
						return all_arr
						break;
					case 1:
						let temArr = []
						all_arr.map((food) => {
							if(food.rateType === 0) {
								temArr.push(food)
							}
						} )
						return temArr
						break;
					case 2:
						let secondTemArr = []
						all_arr.map((food) => {
							if(food.rateType === 1) {
								secondTemArr.push(food)
							}
						} ) 
						return secondTemArr
						break;		
					
				 }	
			
				
			},
			detailCountArr() {
			 let allNum = 0
			 let positiveNum = 0	
			 let negativeNum = 0
			 if(!this.rating_food.ratings) {
			 	return [allNum, positiveNum, negativeNum]
			 }
			 this.rating_food.ratings.map(item => {
			 	if(item.rateType === 0) {
			 		allNum++
			 		positiveNum++
			 	}else if(item.rateType === 1){
			 		allNum++
			 		negativeNum++
			 	}
			 })
			 return [allNum, positiveNum, negativeNum]
			}

		},

		methods: {
			addOne() {
				this.$set(this.rating_food, 'count', 1)
				this.$emit('totalPrice', [this.rating_food.count,this.rating_food])
			},
			toggleCarStatus() {
				this.car_status = !this.car_status
			},
			toggleIconCheck() {
				this.icon_check_bool = this.icon_check_bool === 0 ? 1 : 0
				this.content_bool = this.icon_check_bool
			},
			toggleSelectedNum(num) {
				this.selected_content_type = num
			},
			toggleShow() {
				this.$emit('toggleDetailWrapper', this.detail_show)
			},

			totalPrice(good) {
				this.tem_good = good
				this.$emit('totalPrice', this.tem_good)
			},
			init_detal_scroll() {
				this.$nextTick(() => {
		       	this.deatilScroll = new BScroll(this.$refs.good_detail,{
		        scrollY: true,
		        click: true
	        	 })
	   			 })
			}
		},
		filters: {
			formatDate(val) {
				return formatDate('yyyy-MM-DD hh:mm', val)
			}
		},
		watch: {
			rating_food: {
				handler() {
					if(!this.deatilScroll) {
						this.init_detal_scroll()
					}else {
						this.$nextTick(() => {
							this.deatilScroll.refresh()
						})
						
					}
				}
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/mixin.styl'
    .move-enter,.move-leave-to
     opacity: 0.5	
     transform: translate3D(100%, 0, 0)
    .move-enter-active,.move-leave-active
     transition: all 0.3s linear	
	.good_detail	
	 position: absolute	
	 overflow: hidden
	 padding: 0
	 margin: 0
	 left: 0px
	 top: 0px
	 width: 100%
	 height: 100%
	 background: #fff
	 .back
	  position: absolute
	  padding: 3px
	  z-index: 999
	  left: 5px
	  top: 20px	
	 .avatar_warpper
	  position: relative	
	  padding-top: 100%
	  .avatar
	   position: absolute
	   width: 100%
	   height: 100%
	   left: 0px	 	
	   top: 0px
	 .content
	  padding: 18px 0px
	  .food_info
	   position: relative
	   margin: 0px 18px
	   .name
	   	line-height: 14px
	   	font-size: 14px
	   	font-weight: 700
	   	color: rgb(7, 17, 27)
	   .sell_rating
	   	margin-top: 8px
	    .sellCount,.rating
	     line-height: 10px			
	     font-size: 10px
	     color: rgb(147, 153, 159)
	     margin-right: 12px
	   .pay_price
	   	font-size: 0px
	   	margin: 18px 0px	  
	    .price
	     line-height: 24px
	   	 font-size: 14px
	   	 font-weight: 700	
	   	 color: rgb(240, 20, 20)
	   	.old_price
	   	
	   	 line-height: 24px	 
	   	 font-size: 10px
	   	 font-weight: normal
	   	 color: rgb(147, 153, 159)
	   	 text-decoration: line-through
	   .shop_car_bg
	   	background-color: #00a0dc 
	   .shop_car
	   	padding: 6px 12px	
	   	right: 18px
	   	bottom: 18px
	   	line-height: 24px
	   	border-radius: 17px
	   	font-size: 12px
	   	text-align: center
	   	position: absolute
	   	color: #fff
	   	// background-color: #00a0dc
	   	.increase_and_reduce	
	   	 width: 100px;
	   
	   	 	 
	  .desc_info
	   margin-bottom: 18px	
	   padding: 0px 18px	 
	   .title
	   	font-size: 14px
	   	font-weight: 700
	   	color: #07111b
	   .info
	   	line-height: 24px
	   	font-size: 12px 
	   	font-weight: 200	
	   	color: rgb(77, 85, 93)
	  .line
	   margin-bottom: 18px
	   border-top: 1px solid rgba(7, 17, 27, 0.1) 	
	   border-bottom: 1px solid rgba(7, 17, 27, 0.1) 
	   height: 16px
	   background-color: #f3f5f7
	  .detail_selected_warpper
	   position: relative	
	   margin: 0px 18px
	   padding-bottom: 18px
	   &::after
     	content: '' 
     	position: absolute
     	box-sizing: border-box
     	// margin: 0 12px
     	left: 0  
     	bottom: 0
     	width: 100%
     	border-1px(rgba(7, 17, 27, 0.1))
	  .rating_content
	   .selected_type
	   	margin-left: 16px
	   	position: relative
	   	height: 48px
	   	line-height: 48px
	    &::after
     	 content: '' 
     	 position: absolute
     	 box-sizing: border-box
     	 // margin: 0 12px
     	 left: 0  
     	 bottom: 0
     	 width: 100%
     	 border-1px(rgba(7, 17, 27, 0.1))	
	   	.icon-check_circle
	   	 vertical-align: middle	
	   	 font-size: 24px
	   	 line-height: 24px
	   	 color: rgb(147, 153, 159)
	   	 &.active
	   	  color: #00a0dc	
	   	.text
	   	 margin-left: 4px	
	   	 vertical-align: middle	
	   	 line-height: 24px	
	   	 font-size: 12px
	   	 color: rgb(147, 153, 159)	 	
	   .rating_food_wapper
	   	margin-left: 18px
	   	margin-right: 12px
	    .rating_food
	     position: relative
	     padding-bottom: 20px
	     &::after
     	  content: '' 
     	  position: absolute
     	  box-sizing: border-box
     	  // margin: 0 12px
     	  left: 0  
     	  bottom: 0
     	  width: 100%
     	  border-1px(rgba(7, 17, 27, 0.1))	 	
	     .rateTime
	      margin-top: 16px	
	      margin-bottom: 6px
	      line-height: 12px
	      font-size: 10px
	      color: rgb(147, 153, 159)
	     .text_wrapper
	       line-height: 24px	
	       font-size: 12px
	      .icon-thumb_down
	       color: rgb(147, 153, 159)
	      .icon-thumb_up
	       color: rgb(0, 160, 220)	
	     .phone_icon
	      position: absolute	  	 		
	      top: 0px
	      right: 0px
	      .username
	       margin-right: 2px	
	      
	   			  	 
	   

	   
 	   
</style>


