<template>
	<div class="header">
		<div class="disrtibution">
			<div class="brand">
				<img :src="seller.avatar" width="64" height="64">
			</div>
			<div class="info">
				<div class="title">
				<span class="iconWrapper">
				</span>	
				<span class="name">{{seller.name}}</span>
				</div>
				<div class="description">
					{{seller.description}}
				</div>
				<div class="supports"  v-if="seller.supports">
					<!-- <span class="icon-wrapper" :class="classMap[seller.supports[0].type]"></span> -->
					<supports-type-class class="icon-wrapper" :sunpportsType="sunpportsTypeNum"></supports-type-class>
					<span class="description">{{seller.supports[0].description}}</span>
				</div>
			</div>
			<div class="supportsCount" v-if="seller.supports" @click="handleShowStore">
			<span class="count">
				{{seller.supports.length}}个
			</span>
			<i class="icon icon-keyboard_arrow_right"></i>
			</div>
		</div>
		<div class="bulletinWrapper" @click="handleShowStore">
			<span class="bulBrand">
			</span><span class="desc">
				{{seller.bulletin}}
			</span>
			<span class="icon-wrapper">
				<i class="icon icon-keyboard_arrow_right"></i>
			</span>
		</div>
		<div class="background">
			<img :src="seller.avatar" width="100%" height="100%">
		</div>
		
		<transition name="showStroe">
		<div class="store-shop-info" v-show="showStroe">
			<div class="content-wrapper">
			<div class="content">
				<h2 class="name">{{seller.name}}</h2>
				<div class="star-wrapper">
					<star-score :scopeNum="seller.foodScore"></star-score>
				</div>
				<div class="supports-title">
						<strip title="优惠信息"></strip>
						<ul class="supports-item-wrapper" v-if="seller.supports">
							<li class="supports-item" v-for="(item, $index) in seller.supports">
								<supports-type-class class="icon-box" :sunpportsType="item.type"></supports-type-class>
								<span class="description">{{item.description}}</span>
							</li>
						</ul>
				</div>
				<strip title="商家公告"></strip>
				<div class="bulletin">
					{{seller.bulletin}}
				</div>
			</div>
		  </div>
		  <div class="icon-wrapper">
		  	<i class="icon-close" @click="handleCloseStore"></i>
		  </div>
		</div>
	 </transition>
	</div>
</template>

<script type="text/javascript">
import supportsTypeClass from '@/components/supportsTypeClass/supportsTypeClass.vue'
import starScore from '@/components/star-score/star-score.vue'
import strip from '@/components/strip/strip.vue'
	export default {
		props: {
		 'seller': {
		 	type: Object
		 }
		},

		data() {
			return {
				showStroe: false
			}
		},

		created() {
			console.log('this.seller', this.seller)
			// this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
		},

		methods: {
			handleCloseStore() {
				this.showStroe = false
			},

			handleShowStore() {
				this.showStroe = true
			}
		},

		computed: {
			sunpportsTypeNum() {
				if (this.seller.supports) {
					return this.seller.supports[0].type
				}else {
					return 0
				}
				
			},

			supportsArr() {
				if (this.seller.supports) {
					return this.seller.supports
				}else {
					return []
				}
			}
		},

		components: {
			supportsTypeClass,
			starScore,
			strip
		}
		
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/mixin.styl";
	.header
	 position: relative	
	 overflow: hidden
	 background-color: rgba(7, 17, 27, 0.5)
	 font-size: 0px
	 .disrtibution
	  position: relative		
	  margin: 24px 12px 18px 24px 
	  .supportsCount
	   position: absolute
	   right: 12px
	   bottom: -3px
	   padding: 0px 8px
	   height: 24px
	   line-height: 24px
	   border-radius: 14px
	   background-color: rgba(0, 0, 0, 0.2)
	   font-size: 0px
	   color: rgb(255, 255, 255)
	   .count
	   	vertical-align: top
	   	font-size: 10px
	    font-weight: 200
	   .icon
	   	vertical-align: top
	   	display: inline-block
	   	line-height: 24px
	   	font-size: 10px   
	  .brand, .info	  
	   display: inline-block
	   vertical-align: top
	  .info	
	   margin-left: 16px
	   .title
	   	margin: 2px 6px 8px 0px
	   .iconWrapper
	   	display: inline-block
	   	margin-right: 6px
	   	vertical-align: top
	   	width: 30px
	   	height: 18px
	   	bg-img('./brand')
	   	background-size: 30px 18px
	   .name
	   	display: inline-block;
	   	vertical-align: middle
	   	font-size: 16px
	   	color: rgb(255, 255, 255)
	   	font-weight: bold			 
	   	line-height: 18px
	   .description
	   	margin-bottom: 10px
	   	font-size: 12px
	   	line-height: 12px
	   	color: rgb(255, 255, 255)
	   	font-weight: 200
	   .supports
	   	margin_bottom: 2px;
	   	.icon-wrapper
	   	 display: inline-block
	   	 width: 12px
	   	 height:12px
	   	 background-size: 12px 12px
	   	 vertical-align: top
	   	.description	
	   	 margin-left: 4px
	   	 vertical-align: top
	   	 font-size: 10px
	   	 font-weight: 200
	   	 color: rgb(255, 255, 255)
	 .bulletinWrapper
	  position: relative	
	  overflow: hidden
	  white-space: nowrap
	  text-overflow: ellipsis 
	  font-size: 12px	
	  height: 28px
	  line-height: 28px
	  padding: 0px 22px 0 12px
	  color: rgb(255, 255, 255)	
	  background-color: rgba(7, 17, 27, 0.2)	 
	  .bulBrand
	   display: inline-block
	   vertical-align: top
	   margin-top: 8px
	   bg-img('./bulletin')
	   background-size: 22px 12px	
	   height: 12px
	   width: 22px	 	 	
	  .desc
	   vertical-align: top
	   margin-left: 4px	
	   font-size: 10px
	  .icon-wrapper	 
	   position: absolute
	   right: 12px	 
	   bottom: 8px
	   font-size: 10px
	 .background
	  position: absolute
	  overflow: hidden
	  filter: blur(10px)
	  // opacity: 0.5
	  z-index: -1
	  width: 100%
	  height: 100%
	  top: 0	  	
	  left: 0
	 .showStroe-leave-active,.showStroe-enter-active
	  transition: all 0.5s linear
	 .showStroe-enter,.showStroe-leave-to
	  opacity: 0	   
	 .store-shop-info
	  position: fixed;
	  overflow: auto
	  box-sizing: border-box
	  padding: 64px 32px
	  z-index: 100
	  left: 0
	  top: 0
	  width: 100%
	  height: 100%
	  background: rgba(7, 17, 27, 0.8)	
	  font-size: 12px
	  color: #fff
	  .content-wrapper
	   min-height: 100%	
	   .content
	    height: 100%
	    width: 100%
	    .bulletin 
	     margin: 0 12px	
	     color:#fff
	     font-size: 12px
	     line-height: 24px
	     font-weight: 200
	    .name
	     margin-bottom: 16px
	     text-align: center	
	     font-size: 16px
	     font-weight: 700
	     line-height: 16px
	    .star-wrapper
	     text-align: center	 	
	    .supports-title
	     // .strip
	     //  display: flex
	     //  align-items: center
	     //  width: 100%
	     //  // background: green
	     // .line
	     //  flex: 1;
	     //  height:1px;
	     //  display: inline-block
	     //  background-color: rgba(255, 255, 255, 0.2);
	     //  // vertical-align: middle
	     // .title	
	     //  margin: 0 12px
	      // vertical-align: middle 	 		
	     .supports-item-wrapper	
	      .icon-box
	       display: inline-block
	       width: 16px
	       height: 16px
	       background-size: 16px 16px
	       margin-right: 6px
	       margin-bottom: 12px
	       vertical-align: top
	       &:last-child
	       	margin-right: 0px
	       	margin-bottom: 0px
	      .description
	       line-height: 12px
	       font-size: 12px
	       font-weight: 200px
	       vertical-align: top
	   
	  .icon-wrapper
	  	box-sizing: border-box
	  	margin-bottom: -64px
	  	text-align: center
	  	height: 64px
	  	line-height: 64px 
	   .icon-close
	   	padding: 5px
	   	line-height: 64px
	   	font-size: 32px
	   	color: rgba(255, 255, 255, 0.5)
</style>


