export function formatDate(val, str) {
	let yBool = /(y+)/i.test(val)
	let date = new Date(str)

	if(yBool) {
		let year = date.getFullYear() + ''
		 val = val.replace(RegExp.$1, year.substring(year.length - RegExp.$1.length))
	}

	let o = {
		'(M+)': date.getMonth() + 1,
		'(D+)': date.getDate(),
		'(h+)': date.getHours(),
		'(m+)': date.getMinutes()
	}

	for(let key in o) {

		if(new RegExp(`${key}`).test(val)) {
			let dateStr = o[key]+''
			val = val.replace(RegExp.$1, RegExp.$1.length === 1 ? dateStr : padZero(dateStr) )
		}
	}
	return val

}

function padZero(str) {
	return ('00'+str).substring(str.length)
}


