<template>
	<div class="detail_selected">
		<div class="positive" :class="{active: active === 0}" @click="toggleActive(0)"><span class="text">{{message.all}}</span><span class="selected_type_count">{{detailCountArr[0]}}</span></div>
		<div class="normal" :class="{active: active === 1}" @click="toggleActive(1)"><span class="text">{{message.positive}}</span><span class="selected_type_count">{{detailCountArr[1]}}</span></div>
		<div class="negative" :class="{active: active === 2}" @click="toggleActive(2)"><span class="text">{{message.negative}}</span><span class="selected_type_count">{{detailCountArr[2]}}</span></div>
	</div>
</template>
<script type="text/javascript">
	const ALL = 0
	const POSITIVE = 1
	const NAGATIVE = 2
	export default {
		props: {
			message: {
				type: Object,
				default: ()=> {
					return {
						all: '全部',
						positive: '满意',
						negative: '不满意'
					}
				}
			},
			detailCountArr: {
				type: Array,
				default:() => {
					return [0, 0, 0]
				}

			}
		},

		data() {
			return {
				active: 0
			}
		},

		methods: {
			toggleActive(activeStatus) {
				console.log(1445)
				this.active = activeStatus
				this.$emit('toggleSelectedNum', activeStatus)
			}
		}
	}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
	.detail_selected
	  font-size: 0px
	 .positive,.normal,.negative
	  display: inline-block
	  margin-right: 8px
	  padding: 8px 12px
	  line-height: 16px
	  text-align: center	
	  font-size: 12px
	  border-radius: 1px
	  color: rgb(77, 85, 93)
	  background-color: #ccecf8
	  &.active
	   color: #e9f4fb
	   background-color: #00a0dc
	  .text
	   vertical-align: middle 
	  .selected_type_count
	   margin-left: 4px
	   font-size: 8px;
	   vertical-align: middle 
	 .negative
	  background-color: #e9ebec	  	
	  color: #4d555d
	  &.active
	   background-color: #4d555d
	   color: #fff
	  
	   	 		
	 
	  	 
	 		
</style>


