<template>
  <div class="cat_menu">
    <transition name="move">
      <div class="cat_menu_warpper" v-show="nemu_show">
        <div class="cat_menu_header">
          <div class="left_cat_shop">购物车</div>
          <div class="right_empty" @click="empty">清空</div>
        </div>
        <div class="cat_menu_content" ref="goods_wrapper">
          <ul class="goods_wrapper">
            <li class="good" v-for="good in seleted_data" v-if="good.count">
              <div class="name">{{good.name}}</div>
              <div class="num">￥{{good | payPrice(good) }}</div>
              <div class="increase_reduce">
                <increase-and-reduce :food="good" @totalPrice="totalPrice"></increase-and-reduce>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </transition>
    <div class="bg_dialong" v-show="nemu_show" @click.stop="hideMenu"></div>
  </div>
</template>
<script type="text/javascript">
import increaseAndReduce from "@/components/increaseAndReduce/increaseAndReduce";
import BScroll from "better-scroll";
export default {
  props: {
    seleted_data: {
      type: Array
    }
  },
  data() {
    return {
      nemu_show: false
    };
  },
  created() {
    this.$nextTick(() => {
      console.log(this.$refs.goods_wrapper);
      this.menu_scroll = new BScroll(this.$refs.goods_wrapper, {
        scrollY: true,
        click: true
      });
    });
  },
  methods: {
    hideMenu() {
      this.nemu_show = false;
    },
    empty() {
      this.seleted_data.splice(0);
      this.nemu_show = false;
      this.$emit("empty");
    },
    toggleMenu(val) {
      this.nemu_show = val;
    },
    totalPrice(val) {
      let index = this.seleted_data.findIndex(food => {
        return (
          food.name === val[1].name &&
          food.info === val[1].info &&
          food.description === val[1].description
        );
      });
      val[1].count = val[0];
      console.log(val[1]);
      if (val[0] !== 0) {
        this.seleted_data.splice(index, 1, val[1]);
      } else {
        this.seleted_data.splice(index, 1);
      }
    }
  },
  filters: {
    payPrice: function(good) {
      console.log(good);
      return good.price * good.count;
    }
  },

  watch: {
    seleted_data: {
      handler() {
        if (!this.menu_scroll) {
          this.$nextTick(() => {
            this.menu_scroll = new BScroll(this.$refs.goods_wrapper, {
              scrollY: true,
              click: true
            });
          });
        } else {
          this.$nextTick(() => {
            this.menu_scroll.refresh();
          });
        }
      },
      deep: true
    }
  },

  components: {
    increaseAndReduce
  }
};
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import '~common/stylus/mixin.styl';

.cat_menu {
  height: 100%;
  overflow: hidden;
}

.cat_menu_warpper {
  position: absolute;
  z-index: -1;
  box-sizing: border-box;
  overflow: hidden;
  width: 100%;
  left: 0px;
  overflow: hidden;
  bottom: 48px;
  // transform: translateY(100%)
}

.move-enter, .move-leave-to {
  transform: translateY(100%);
  // opacity: 0
}

// .move-enter-to,.move-leave
// transform: translateY(0px)
.move-enter-active, .move-leave-active {
  transition: all 0.3s linear;
}

.cat_menu_header {
  position: relative;
  padding: 0px 18px;
  overflow: hidden;
  height: 40px;
  background-color: #f3f5f7;
  line-height: 40px;

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    left: 0px;
    bottom: 0px;
    border-1px(rgba(7, 17, 27, 0.1));
    // border-1px(red)
  }

  .left_cat_shop {
    float: left;
    font-size: 14px;
    color: rgb(7, 17, 17);
  }

  .right_empty {
    float: right;
    font-size: 14px;
    color: rgb(0, 140, 220);
  }
}

.cat_menu_content {
  position: relative;
  max-height: 150px;
  overflow: hidden;
  background-color: #fff;

  .goods_wrapper {
    .good {
      position: relative;
      display: flex;
      margin: 0px 18px;
      height: 48px;
      line-height: 48px;

      &::after {
        content: '';
        position: absolute;
        width: 100%;
        left: 0px;
        bottom: 0px;
        border-1px(rgba(7, 17, 27, 0.1));
      }

      &:last-child {
        margin-bottom: 0px;
      }

      .name {
        flex: 1;
        font-size: 14px;
        font-weight: 700;
        color: rgb(7, 17, 27);
        line-height: 48px;
      }

      .num {
        flex: 0 0 24px;
        margin-right: 12px;
        color: rgb(240, 20, 20);
        font-size: 14px;
        font-weight: 700;
      }

      .increase_reduce {
        flex: 0 0 100px;
      }

      .increase_reduce {
        position: relative;
        top: 30px;
      }
    }
  }
}

.bg_dialong {
  position: fixed;
  z-index: -2;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  filter: blur(10px);
  background: rgba(7, 17, 27, 0.6);
}
</style>


