<template>
	<span class="sunpportsTypeClass" :class="classMap[sunpportsType]">
	</span>
</template>
<script type="text/javascript">
	export default {
		props: {
			'sunpportsType': {
				type: Number,
				default: 0
			}
		},

		created() {
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/mixin.styl";
   	.decrease
   	 bg-img('./decrease_1')
	.discount	
	 bg-img('./discount_1')
	.special	
	 bg-img('./special_1')
	.invoice	
	 bg-img('./invoice_1')
	.guarantee	
	 bg-img('./guarantee_1') 	
</style>


