import starScore from '@/components/star-score/star-score'
import supportsTypeClass from '@/components/supportsTypeClass/supportsTypeClass'
import BScroll from 'better-scroll'
export default {
	props: {
	 'goods': {
				type: Array
	   },
      'seller': {
        type: Object
       }
	},
	components: {
		starScore,
		supportsTypeClass
	},
	data() {
		return {
			active: 0
		}
	},
	created() {
		console.log('seller', this.seller)
		this.active = window.localStorage.getItem('favorite') || 0

		this.$nextTick(() => {
	               this.storeScorll = new BScroll(this.$refs.store, {
	                scrollY: true,
	                click: true
	              
	             })

	               this.picsScroll = new BScroll(this.$refs.pics_scroll_wrapper, {
	               	scrollX: true,
	               	click: true
	               })
	               // console.dir(this.$refs.pics_scroll_wrapper)
	               let pic_item = this.$refs.pic_item
	               let margin_right_num = parseInt(window.getComputedStyle(this.$refs.pic_item[0], null).marginRight)
	               this.$refs.pics_wrapper.style.width = ((pic_item[0].offsetWidth+ margin_right_num) * pic_item.length - margin_right_num) + 'px' 
	               // this.$refs.pics_wrapper.style.height = 90 + 'px'
	            })
	},

	methods: {
		toggleActive() {
			this.active = !this.active
			if(!window.localStorage.getItem('favorite')) {
				window.localStorage.setItem('favorite', this.active)
			}else{
				window.localStorage.favorite = this.active
			}
			
		}
	},

	watch: {
		seller:{
			handler(){
				this.$nextTick(() => {
	               this.storeScorll = new BScroll(this.$refs.store, {
	                scrollY: true,
	                click: true
	              
	             })

	               this.picsScroll = new BScroll(this.$refs.pics_scroll_wrapper, {
	               	scrollX: true,
	               	click: true
	               })
	               // console.dir(this.$refs.pics_scroll_wrapper)
	               let pic_item = this.$refs.pic_item
	               let margin_right_num = parseInt(window.getComputedStyle(this.$refs.pic_item[0], null).marginRight)
	               this.$refs.pics_wrapper.style.width = ((pic_item[0].offsetWidth+ margin_right_num) * pic_item.length - margin_right_num) + 'px' 
	               // this.$refs.pics_wrapper.style.height = 90 + 'px'
	            })
			},
			deep: true
		}
	}

}


