<template>
	<div class="increaseAndReduce">
		
		<div class="btn_wrapper">
			<transition name="move">
			<span class="reduce icon-remove_circle_outline"  v-show="food.count" @click.shop="reduce($event)">
			</span>
			</transition>
			<transition name="opac">	
			<span class="text" v-show="food.count">{{food.count}}</span>
			</transition>	
			<span class="increase icon-add_circle" @click.shop="increase($event)"></span>
			
		</div>
	
	</div>
</template>
<script type="text/javascript">
	export default {
		props: {
			'food': {
				type: Object
			}
		},
		data() {
 			return {
 				num: 0
 			}
		},

		// created() {
		// 	this.num = this.food.count || 0
		// },

		methods: {
			increase(ev) {
				// this.num++
				if(!this.food.count) {
				 this.$set(this.food, 'count', 1)
				}else{
				 
				let num = this.food.count + 1
				delete this.food.count
				this.$set(this.food, 'count', num)	
				}
				this.$emit('totalPrice', [this.food.count, this.food, ev])
			},

			reduce(ev) {
				if(this.food.count === 0) {
					return
				}
				let num = this.food.count -1
				delete this.food.count
				this.$set(this.food, 'count', num)
				this.$emit('totalPrice', [this.food.count, this.food])
				
			}

		}
	}
</script>
<style scoped rel="stylesheet/stylus" lang="stylus" >
	.increaseAndReduce
	 position: absolute
	 right: 18px
	 bottom: 18px 
	 .move-enter,.move-leave-to
	  opacity: 0
	  transform: translateX(24px) rotate(360deg) 
	 .move-enter-active,.move-leave-active
	  transition: all 0.5s 
	 .icon-remove_circle_outline,.icon-add_circle
	  display: inline-block	
	  vertical-align: top	
	  font-size: 24px
	  color: rgb(0, 160, 220) 		
	  line-height: 24px
	 
	 
	  // opacity: 0
	 .opac-enter,.opac-leave-to
	  opacity: 0
	 .opac-enter-active,.opac-leave-active
	  transition: all 0.5s  	
	 .text
	  display: inline-block
	  vertical-align: top
	  width: 24px
	  text-align: center
	  color: rgb(147, 153, 159)	 
	  font-size: 10px
	  line-height: 24px
</style>


