<template>
<div class="strip">
	<div class="line"></div>
	<div class="title">{{title}}</div>
	<div class="line"></div>
</div>
</template>
<script type="text/javascript">
	export default {
		props: {
			title: {
				type: String,
				default: '优惠信息'
			}
		}
	}
</script>
<style lang="stylus" rel="stylesheet/stylus">
.strip
 display: flex
 align-items: center
 width: 100%
 margin: 28px 0 24px 0
  // background: green
 .line
  flex: 1;
  height:1px;
  display: inline-block
  background-color: rgba(255, 255, 255, 0.2);
  // vertical-align: middle
 .title	
  margin: 0 12px
</style>


