<template>
  <div id="app">
    <div class="header">
        <v-header :seller="seller.seller"></v-header>
    </div>
    <div class="navBar">
      <router-link to="/good"  class="nav_item">商品</router-link>
      <router-link to="/rating" class="nav_item">评价</router-link>
      <router-link to="/store" class="nav_item">商家</router-link>
    </div>
    <div class="content">
      <keep-alive>
        <router-view :goods="goods" :seller="seller"></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import vHeader from '@/components/header/header'
import seller from '../data.json'
export default {
  name: 'App',
  data () {
    return {
      seller: seller,
      goods: []
    }
  },
  created() {
    // this.getStoreData()
    this.getGoodsData()
  },
  methods: {
    getStoreData() {
      this.$http.get('/data.json').then((res) => {
       this.seller = res.data
       console.log(this.seller)
      })
    },

    async getGoodsData() {
    //  const res = await this.$http.get('/api/goods.json')
    //  console.log(res)
    //  this.goods = res.data
      this.goods = this.seller.goods
    }
  
  },

  components: {
    vHeader
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus"> 
@import '~common/stylus/mixin.styl'
 #app
  height: 100%
  .navBar
   display: flex
   position: relative
   align-items: center;
   height: 40px
   font-size: 14px
   line-height: 14px
   &::after
    content: ''
    display: block
    position: absolute
    bottom: 0px
    width: 100%
    left: 0px
    // border-bottom: 1px solid rgba(7, 17, 27 ,0.1)
    border-1px(rgba(7, 17, 27 ,0.1))
   .nav_item
    height: 100%
    line-height: 40px 
    flex: 1;
    text-align: center
    color: rgb(77, 85, 93)
   .active
    color: rgb(240, 20, 20)  
</style>



