<template>
	<div class="cart_shop">
		<div class="left_content">
			<div class="icon_round">
				<div class="avatar">
					<div class="icon_wrapper" :class="{active: totalPrice}">
						<i class="icon-shopping_cart" :class="{icon_active: totalPrice}"></i>
					</div>
					<span class="count" v-if="totalCount">{{totalCount}}</span>
				</div>
			</div>
			<div class="pay_price_wrapper">
				<div class="pay_price" :class="{payNum: totalPrice}">￥{{totalPrice}}</div>
			</div>	
			<div class="desc" @click="showGoodsMenu">另需配送费￥{{seller.deliveryPrice}}元</div>
		</div>
		<div class="right_pay" v-if="diffMinPrice === 0" >
			￥{{seller.minPrice}}起送
		</div>
		<div class="right_pay" v-else-if="diffMinPrice === 1">
			还差￥{{seller.minPrice-totalPrice}}元起送
		</div>
		<div class="right_pay gopay" v-else-if="diffMinPrice === 2" @click="buy">
			去结算
		</div>
		<div class="ball_wrapper">
			<transition-group
			v-on:before-enter="beforeEnter"
  			v-on:enter="enter"
  			v-on:after-enter="afterEnter">
 		   <div ref="ball" class="ball_box" v-show="item.status" v-for="(item,index) in ballData" :key="index">

		   <span class="ball"> </span> 
		   </div>	
			</transition-group>
		</div>
	</div>
</template>
<script type="text/javascript">
	let x = 0
	let y = 0
	const BALL_COUNT = 5
	export default {
		props: {
			'sleted_data': {
				type: Array
			},
			'seller': {
				type: Object
			}
		},

	   data() {
	   	return {
	   		ballData: [
	   		  { status: 0},
	   		  { status: 0},
	   		  { status: 0},
	   		  { status: 0},
	   		  { status: 0}
	   		  	
	   		],
	   		dorpIndex: 0,
	   		delData: [],
       	    show: false
	   	}
	   },

	   created() {
	   	this.innit_ball()
	   },

	   methods: {
	   	showGoodsMenu() {

        	this.show = !this.show
        	this.$emit('showGoodsMenu', this.show)
      	},
	   	buy() {
	   		window.alert(`本次购物您应该付款${this.totalPrice}元`)
	   	},
	   	innit_ball() {
	   		this.$nextTick(() => {
	   			let ballTemArr = this.$refs.ball
	   			this.ballData.forEach( (ball,index) => {
	   			 ball.ele = ballTemArr[index]

	   		  })
	   			
	   			this.$emit('dropball', this.ballData)

	   		})
	   	},

	   	dorpBall(valObj) {
	   		x = valObj.getBoundingClientRect().x
	   		y = valObj.getBoundingClientRect().y
	   		let count = this.ballData.length
	   		for(let i=this.dorpIndex; i<count; i++) {
	   			if(this.ballData[i].status === 0) {
	   				this.dorpIndex++
	   				if(this.dorpIndex > BALL_COUNT-1) {
	   					this.dorpIndex = 0
	   				}
	   				let ball = this.ballData[i]
	   				ball.status = 1
	   				this.delData.push(ball)
	   				return
	   			}
	   		}
	   	},
	   	beforeEnter: function (el) {
	   		let count = this.ballData.length
	   		while(count--) {
	   			let ball = this.ballData[count]
	   			if(ball.status === 1) {
	   			 let inner = el.getElementsByClassName('ball')[0]
	   			 y = window.innerHeight - y-40
	   			 x = x -18
	   			 	el.style.opacity = 0
	   			  el.style.transform =`translate3D(0px, ${-y}px, 0px)`
	   			  inner.style.transform =`translate3D(${x}px, 0px, 0px)`
	   			}
	   		}
	   	},
	   	enter: function (el, done) {
	   		let inner = el.getElementsByClassName('ball')[0]
	   		
	   		el.style.opacity = 1
	   		el.offsetWidth;
	   		inner.offsetWidth;
	   		el.style.transform =`translate3D(0px, 0px, 0px)`
	   		inner.style.transform =`translate3D(0px, 0px, 0px)`
            el.style.transition = "all 0.3s cubic-bezier(.85,.08,1,.42)"
            inner.style.transition = "all 0.3s linear"
  
	   		done()
	   		
	   	   		
	   	},
	   	afterEnter: function (el) {
	   		let ball = this.delData.shift()
	   		if(ball) {

	   			ball.status = 0
	   		}
	   		
	   	}


	   },

		computed: {
			totalPrice() {
				let totalPrice = 0
				// this.sleted_data.forEach(item => {
				// 	totalPrice += item.count * item.price
				// })
				for(let i=0;i <this.sleted_data.length; i++) {
					totalPrice += this.sleted_data[i].count * this.sleted_data[i].price
				}
				return totalPrice
			},

			totalCount() {
				let totalCount = 0
				for(let i=0;i <this.sleted_data.length; i++) {
					totalCount += this.sleted_data[i].count 
				}
				return totalCount
			},
			
			diffMinPrice() {
				if(this.totalPrice === 0) {
					return 0
				}else if(this.totalPrice < this.seller.minPrice){
					return 1
				}else {
					return 2
				}
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
	.cart_shop
	 display: flex	
	 height: 48px
	 width: 100%
	 background-color: #141d27
	 .left_content
	  flex: 1
	  display: flex
	  align-items: center
	  .icon_round
	   flex: 0 0 44px
	   position: relative		
	   // margin-left: 18px
	   border-radius: 50%
	   width: 44px
	   height: 44px
	   // background-color: rgba(255, 255, 255, 0.4);
	   .avatar 
	    position: absolute
	    text-align: center
	    border-radius: 50%
	    left:0px
	    bottom: 0px
	    width: 58px
	    height: 58px
	    background: #141d27
	    .count
	     position: absolute;
	     padding-top: 2px
	     top: 0px;
	     left: 33px;
	     height: 12px
	     line-height: 12px;
	     min-width: 25px
	     font-size: 10px;
	     text-align: center;
	     color: #fff
	     border-radius: 4px;
	     background-color: #f00;
	     
	    .icon_wrapper
	     margin-top: 6px
	     display: inline-block
	     border-radius: 50%	
	     width: 44px
	     height: 44px
	     background-color: #2b343c
	     &.active
	      background-color: #00a0dc	
	     .icon-shopping_cart
	      font-size: 25px
	      line-height: 44px
	      color: #80858a
	      &.icon_active
	       color: #fff	
	    	
	  .pay_price_wrapper	
	   margin-left: 16px
	   .pay_price
	   	// box-sizing: border-box
	   	display: inline-block
	   	line-height: 30px	
	   	height: 30px
	   	border-right: 1px solid rgba(255, 255, 255, 0.1) 	
	   	padding-right: 18px	
	   	font-size: 16px
	   	font-weight: 700
	   	color: rgba(255, 255, 255, 0.4)
	   	&.payNum
	   	 color: #fff	
	  .desc
	   margin-left: 10px
	   // margin-right: 12px
	   font-size: 14px
	   color: rgba(255, 255, 255, 0.4)
	 .right_pay
	  flex: 0 0 105px
	  padding: 0px 8px
	  text-align: center
	  line-height: 48px
	  font-size: 12px
	  color: rgba(255, 255, 255, 0.4)	 
	  font-weight: 700
	  background-color: rgb(43, 51, 59)
	  &.gopay
	    background-color: #00a0dc
	    color: #fff	
	 .ball_wrapper
	  position: absolute
	  left: 21px
	  .ball
	   position: absolute	 
	   width: 15px
	   height: 15px
	   border-radius: 50%
	   background-color: green
	   	  
</style>


