import detailSelected from '@/components/detail_selected/detail_selected' 
import starScope from '@/components/star-score/star-score.vue' 
import BSsroll from 'better-scroll'
// import seller from '../../../data.json'
import {formatDate} from 'common/js/formatDate.js'
export default {
	props: {
		seller: {
			type: Object
		}
	},
	data() {
		return{
			ratings_message: {
			 all: '全部',
 		     positive: '满意',
 			 negative: '不满意'	
			},
 			selected_content_type: 0,
 			ratings: [],
 			icon_check_bool: 0,
 			content_bool: 0,
 			icon_check_bool: 0
		}

	},

	components: {
		detailSelected,
		starScope
	},
	created() {
		this.getRatingData()
		this.init_BSscroll()
	},
	filters: {
		formatDate(val) {
			return formatDate('YYYY-MM-DD hh:mm', val)
		}
	},
	watch: {
		ratings: {
			handler() {
				if(!this.ratingScroll) {
					this.$nextTick( () => {
					  this.ratingScroll = new BSsroll(this.$refs.rating, {
							 scrollY: true,
						     click: true
						})
					})
				}else {
					this.$nextTick( () =>{
					  this.ratingScroll.refresh()
					}) 
					 
				}
			}
		}
	},

	methods: {
	init_BSscroll() {

		this.$nextTick( () => {
			this.ratingScroll = new BSsroll(this.$refs.rating, {
			 scrollY: true,
		     click: true
		})
			
		 // this.$refs.supports.style.width=	
		})
		
	},	
	getRatingData() {
      // this.$http.get('/api/ratings.json').then((res) => {
      //  this.ratings = res.data
			// })
			this.ratings = this.seller.ratings
    },

    toggleIconCheck() {
	  this.icon_check_bool = this.icon_check_bool === 0 ? 1 : 0
	  this.content_bool = this.icon_check_bool
	},
   
	toggleSelectedNum(num) {
	  this.selected_content_type = num

	 }
	},
	computed: {
    detailCountArr() {
 	 let allNum = 0
	 let positiveNum = 0	
	 let negativeNum = 0
	 if(!this.ratings) {
	 	return [allNum, positiveNum, negativeNum]
	 }
	 this.ratings.map(item => {
	 	if(item.rateType === 0) {
	 		allNum++
	 		positiveNum++
	 	}else if(item.rateType === 1){
	 		allNum++
	 		negativeNum++
	 	}
	 })
	 return [allNum, positiveNum, negativeNum]
	},
	rating_arr(){
		let all_arr = []
		if(this.content_bool === 1) {
			this.ratings.map(food => {
				if(food.text) {
					all_arr.push(food)
				}
			})
		}else {
			all_arr = this.ratings
		}
		switch (this.selected_content_type) {
		case 0:
			// return this.ratings
			return all_arr
			break;
		case 1:
			let temArr = []
			all_arr.map((food) => {
				if(food.rateType === 0) {
					temArr.push(food)
				}
			} )
			return temArr
			break;
		case 2:
			let secondTemArr = []
			all_arr.map((food) => {
				if(food.rateType === 1) {
					secondTemArr.push(food)
				}
			} ) 
			return secondTemArr
			break;		
		
	 }	
			
				
			}
	}
}


